.top-box {

    position: relative;
    min-height: 190px;
}


body {
    background-color: cadetblue;
    background: linear-gradient(-45deg, #35577D, #141E30);
    color: aliceblue;
}

h1 {
    font-weight: bold;
}


h2.about {
    padding-top: 15px;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
}


h2 {
    color: rgba(255, 255, 255, 0.5);
}


p {
    padding-bottom: 35px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    text-align: justify;
    margin-left: 1%;
    margin-right: 1%;
}

#pro-pic {
    height: 200px;
    float: left;
    padding-right: 15px;
    padding-left: 10px;
    padding-bottom: 20px;
}

.top {
    float: left;
}

h1.title {
    padding-top: 10px;
}

div.contact {
    display: block;
    text-align: center;
    position: relative;
    width: 100%;
}


form {
    display: inline-block;
    margin-left: 8%;
    margin-right: 8%;
    text-align: left;
    padding-bottom: 100px;
}

label, input, textarea {
    width: 100%;
    overflow: auto;
    resize: none;
}

#submit {
    max-width:fit-content;
}

div.button {
    display: flex;
    justify-content: center;
    align-items: center;
}

.revealright {
    position: relative;
    transform: translateX(-150px);
    opacity: 0;
    transition: 1s all ease;
}
  
.revealright.active {
    transform: translateX(0);
    opacity: 1;
}


.revealup {
    position: relative;
    transform: translateY(150px);
    opacity: 0;
    transition: 1s all ease;
}
  
.revealup.active {
    transform: translateY(0);
    opacity: 1;
}